import React, { useState, useEffect } from 'react';
import axios from 'axios';

const IssueForm = () => {
  const [vehicles, setVehicles] = useState([]);
  const [components, setComponents] = useState([]);
  const [selectedVehicle, setSelectedVehicle] = useState('');
  const [description, setDescription] = useState('');
  const [selectedComponent, setSelectedComponent] = useState('');
  const [issueType, setIssueType] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      try {
        const vehiclesResponse = await axios.get('/api/vehicles/');
        const componentsResponse = await axios.get('/api/components/');
        setVehicles(vehiclesResponse.data);
        setComponents(componentsResponse.data);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post('/api/issues/', { vehicle: selectedVehicle, description, component: selectedComponent, issue_type: issueType });
      // Reset form fields
      setSelectedVehicle('');
      setDescription('');
      setSelectedComponent('');
      setIssueType('');
    } catch (error) {
      console.error("Error submitting issue:", error);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <h2>Add Issue</h2>
      <div>
        <label>Vehicle:</label>
        <select value={selectedVehicle} onChange={(e) => setSelectedVehicle(e.target.value)} required>
          <option value="">Select Vehicle</option>
          {vehicles.map(vehicle => (
            <option key={vehicle.id} value={vehicle.id}>{vehicle.registration_number}</option>
          ))}
        </select>
      </div>
      <div>
        <label>Description:</label>
        <input type="text" value={description} onChange={(e) => setDescription(e.target.value)} required />
      </div>
      <div>
        <label>Component:</label>
        <select value={selectedComponent} onChange={(e) => setSelectedComponent(e.target.value)} required>
          <option value="">Select Component</option>
          {components.map(component => (
            <option key={component.id} value={component.id}>{component.name}</option>
          ))}
        </select>
      </div>
      <div>
        <label>Issue Type:</label>
        <select value={issueType} onChange={(e) => setIssueType(e.target.value)} required>
          <option value="">Select Type</option>
          <option value="repair">Repair</option>
          <option value="new">New</option>
        </select>
      </div>
      <button type="submit">Submit</button>
    </form>
  );
};

export default IssueForm;
