import React, { useState } from 'react';
import axios from 'axios';

const VehicleForm = () => {
  const [ownerName, setOwnerName] = useState('');
  const [vehicleType, setVehicleType] = useState('');
  const [registrationNumber, setRegistrationNumber] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post('/api/vehicles/', { owner_name: ownerName, vehicle_type: vehicleType, registration_number: registrationNumber });
      // Reset form fields
      setOwnerName('');
      setVehicleType('');
      setRegistrationNumber('');
    } catch (error) {
      console.error("There was an error!", error);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <h2>Add Vehicle</h2>
      <div>
        <label>Owner Name:</label>
        <input type="text" value={ownerName} onChange={(e) => setOwnerName(e.target.value)} required />
      </div>
      <div>
        <label>Vehicle Type:</label>
        <input type="text" value={vehicleType} onChange={(e) => setVehicleType(e.target.value)} required />
      </div>
      <div>
        <label>Registration Number:</label>
        <input type="text" value={registrationNumber} onChange={(e) => setRegistrationNumber(e.target.value)} required />
      </div>
      <button type="submit">Submit</button>
    </form>
  );
};

export default VehicleForm;
